<template>
  <div id="app">
    <Navbar />
    <main role="main">
      <router-view />
    </main>
  </div>
</template>

<script>
import Navbar from './components/Navbar.vue';
import ProductsService from './services/API/ProductsService';
import ImagesService from './services/API/ImagesService';
import idbs from './services/IndexedDB/ProductsService';


export default {
  components: {
    Navbar,
  },
  // for github pages; hack to enable routes for spa
  mounted() {
    const path = localStorage.getItem('path');
    if (path) {
      localStorage.removeItem('path');
      this.$router.replace(path);
    }
  },
  created() {
    this.getProducts();
    this.getImages();
  },
  methods: {
    getProducts() {
      ProductsService.getProducts().then((response) => {
        console.log(response);
        this.products = response.data.items;
        idbs.save(response.data.items);
      });
    },
    getImages() {
      ImagesService.getImages().then((response) => {
        console.log(response.data.items[0].fields.file.url);
      });
    },
  },
};
</script>
